.message-box{
position: absolute;
width: 90%;
height: -webkit-fit-content;
height: -moz-fit-content;
height: fit-content;
left: 0px;
bottom: 0;
background: #FFFFFF;
border-right:none; 
-webkit-box-shadow: 0px -5px 15px rgba(0, 0, 0, 0.25); 
        box-shadow: 0px -5px 15px rgba(0, 0, 0, 0.25);
padding: 20px;
border: none;
}

.message-bubble{
    /* position: ; */
    padding-top: 70px
}

.message-btn{
    float: right;
        position: absolute;
        width: 10%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        right: 0px;
        border: none;
        bottom: 0px;
        -webkit-box-shadow: 0px -5px 15px rgba(0, 0, 0, 0.25);
                box-shadow: 0px -5px 15px rgba(0, 0, 0, 0.25);
        background: white;
        padding: 20px;
        padding-right: 20px;
        margin: 1px;
        
}
.form-group{
    display: inline-block;
}


