.chat-bubble{
    position: absolute;
    margin-right: 50px;
    top:30px;
    left: 70px; 
    padding: 10px;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 12pt;
    /* border: 3px solid #FFFFFF; */
    border-radius: 0px 25px;
    -webkit-box-shadow: 2px 3px 0px 0.5px rgba(168, 168, 168, 0.548);
            box-shadow: 2px 3px 0px 0.5px rgba(168, 168, 168, 0.548);
    float: left;
    word-break: break-all;
    text-align: left;
}

.chat-bubble-bot{
 background: #0A62C7;
 border-radius: 20px 0px;
 color: white;
  right: 30px;
  left: unset;
}
.chat-bubble-male{
    background: #D6D6D6;
    -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px 20px;
}

.chat-bubble-female{
 background: #F1B9B9;
 -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
         box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0px 20px;
}


.avater-img{
    border-radius: 50%;
    max-width: 60px;
    float: left;
}

.avater-img-right{
    float: right !important;
}

.Chat{
    position: relative;
    display: inline-block;
    width: 100%;
    margin-bottom: 50px;
    margin-left: 20px;
    
}


